/**
 * Created by liux on 2018/4/25.
 * @param Object,[Array], [boolean]//测试数据对象，可选的检测顺序，可选的静默检测
 * @return boolean //true:通过
 *
 */
const Map = {
  data(value, slient){
    try{
      value = JSON.parse(value)
    }catch(err){
      if (!slient) {
        this.$notify.error({
        title: 'json格式错误',
        message: err
        });
      }
      return false;
    }
    return value
  },
  url(value, slient) {
    if (/^\/[^\s]+$/g.test(value)){
      return true
    }else {
      if (!slient) {
        this.$notify.error({
          title: 'url格式错误',
          message: value
        });
      }
      return false;
    }
  },
  newUrl() {
    return Map.url.call(this,...arguments)
  },
  host(value,slient){
    if (!value || /^[^\/\s]+$/g.test(value)){
      return true
    }else {
      if (!slient) {
        this.$notify.error({
          title: 'host格式错误',
          message: value
        });
      }
      return false;
    }
  },
  store(value,slient){
    if (value && /^[^\/\s]+$/g.test(value)){
      return true
    }else {
      if (!slient) {
        this.$notify.error({
          title: '仓库格式错误',
          message: value
        });
      }
      return false;
    }
  },
  newStore(){
    return Map.store.call(this, ...arguments)
  },
  page(value,slient){
    if (!value || /^\d+$/g.test(value)){
      return true
    }else {
      if (!slient) {
        this.$notify.error({
          title: '页码格式错误',
          message: value
        });
      }
      return false;
    }
  },
  opt(value,slient){
    if (!value.weight || /^\d+$/g.test(value.weight)){
      return true
    }else {
      if (!slient) {
        this.$notify.error({
          title: 'opt格式错误',
          message: JSON.stringify(value)
        });
      }
      return false;
    }
  },
}
export default {
  methods: {
    test(obj, arr, slient){
      if (typeof arr === 'boolean') {
        slient = arr;
        arr = null;
      }
      arr = arr || Object.keys(obj);
      return arr.every((key) => {
        if (typeof Map[key] === 'function') {
          let re =  Map[key].call(this, obj[key], slient);
          if (typeof re !== 'boolean'){
            obj[key] = re;
          }
          return re
        } else {
          return true
        }
      })
    },
  }
}


