<style lang="less" scoped>


    .el-main {
        padding: 10px;
    }

    .box-card {
        position: relative;
        margin-top: 10px;
        .header {
            display: flex;
            margin-bottom: 10px;
            .url-box {
                flex: 1;
                font-size: 18px;
                margin-right: 10px;
                input {
                    font-size: 16px;
                    width: 100%;
                    padding: 8px;
                    border-bottom: 1px solid #dcdfe6;
                }
            }
            .edit {
                width: 90px;
                float: right;
                text-align: right;
            }
        }
        .json-textarea>textarea{
            overflow: hidden;
        }
    }

    .slide-fade-enter-active {
        transform: translateY(0);
        transition: all .2s ease;
    }

    .slide-fade-leave-active {
        transform: translateY(0);
        transition: all .2s ease;
    }

    .slide-fade-enter, .slide-fade-leave-to {
        transform: translateY(100%);

    }

    .slide-fade-move {
        transition: transform .2s ease;
    }

    .add-box {
        animation-duration: .3s;
    }

    .search {
        margin-top: 10px;
        width: 100%;
        padding: 6px;
        display: flex;
        background-color: white;
        position: relative;
        font-size: 18px;
    }
    .err-tip{
        color: #f1592a;
        font-weight:bold;
        padding: 0 10px 10px;
        white-space: pre-line;
        font-family: Menlo,Monaco,Consolas,"Courier New",monospace;
    }

</style>
<template>
    <el-main class="main-box">
        <transition enter-active-class="animated animated-fast slideInDown"
                    leave-active-class="animated animated-fast slideOutUp">
            <el-card v-show="addFlag" class="box-card add-box">
                <div class="header">
                    <div class="url-box">
                        <input type="text" v-model="addJson.url" placeholder="请输入接口地址">
                    </div>
                    <div class="edit">
                        <el-button @click="add" size="small" type="success" icon="el-icon-upload" circle></el-button>
                        <el-button @click="clean" size="small" type="warning" icon="el-icon-minus" circle></el-button>
                    </div>
                </div>
                <div class="err-tip" v-html="jsonlint(addJson.data)">
                </div>
                <el-input
                        type="textarea"
                        :autosize="{minRows: 5}"
                        placeholder="请输入json格式"
                        v-model="addJson.data">
                </el-input>
            </el-card>
        </transition>
        <div class="search">
            <el-input
                    placeholder="请输入api关键字"
                    v-model="search"
                    clearable
            >
            </el-input>
        </div>
        <transition-group name="slide-fade" tag="div">
            <div v-for="(item,index) in showData" :key="item.url" class="box-card">
                <el-card>
                    <div class="header">
                        <div class="url-box">
                            <input type="text" v-model="item.newUrl" placeholder="请输入接口地址">
                        </div>
                        <div class="edit">
                            <el-button @click="update(item.url,item.data,item.newUrl)" size="small" type="success"
                                       icon="el-icon-upload" circle></el-button>
                            <el-button @click="rm(item.url)" size="small" type="danger" icon="el-icon-delete"
                                       circle></el-button>
                        </div>
                    </div>
                    <div class="err-tip" v-html="jsonlint(item.data)">

                    </div>
                    <el-input
                              clearable
                              @focus="reset(index,'focus')"
                              @blur="reset(index,'blur')"
                              ref="input"
                              type="textarea"
                              :autosize="autosize"
                              placeholder="请输入json格式"
                              v-model="item.data">
                    </el-input>
                </el-card>
            </div>
        </transition-group>
    </el-main>

</template>
<script>
  import {mapState, mapGetters} from 'vuex';
  import jsonlint from 'assets/jsonlint'
  import mixin from './mixin'
  export default {
    data() {
      return {
        addJson: {
          url: '',
          data: '',
        },
        autosize: {minRows: 1, maxRows: 2},
        jsonData: [],
        showData: [],
        search: ''
      }
    },
    computed: {
      ...mapState(['addFlag']),
    },
    watch: {
      search(val){
        this.searchInit(val)
      }
    },
    created() {
      window.home = this;
      this.queryJson(0);
    },
    mixins: [mixin],
    methods: {
      reset(index, type){
        if (type === 'blur') {
          this.autosize = {minRows: 1, maxRows: 2};
        } else {
          this.autosize = {minRows: 1, maxRows: 100}
        }
        this.$nextTick(() => {
          this.$refs.input[index].resizeTextarea();
        })
      },
      searchInit(val){
        if (!val) {
          this.showData = this.jsonData;
          return
        }
        if (this.timer) {
          clearTimeout(this.timer);
        }
        this.timer = setTimeout(() => {
          var keys = val.split(/\s+/, 4);
          this.showData = this.jsonData.filter(item => {
            return keys.every(key => {
              return item.url.indexOf(key) != -1
            })
          })
        }, 100)
      },
      queryJson(page){
        this.postSlient('/api/query', {page: page}).then(json => {
          this.format(json.data)
        })
      },
      add(){
        var params = {
          url: this.addJson.url,
          data: this.addJson.data
        }
        if (!this.test(params)) {
          return
        }
        this.post('/api/add', params).then(json => {
          this.format(json.data, true)
        })
      },
      clean(){
        this.addJson.data = '';
        this.addJson.url = '';
      },
      rm(url){
        this.post('/api/rm', {url: url}).then(json => {
          this.jsonData = this.jsonData.filter(item => {
            return item.url !== url
          })
          this.tip('删除成功')
        })
      },
      update(url, data, newUrl){
        var params = {
          url: url,
          data: data,
          newUrl: newUrl
        }
        if (!this.test(params)) {
          return;
        }
        this.postSlient('/api/update', params).then(json => {
          this.tip()
        })
      },
      tip(text){
        this.$message({
          message: text || '数据更新成功',
          type: 'success'
        });
      },
      format(data, add){
        function mat(item) {
          item.data = JSON.stringify(item.data, null, 2);
          item.newUrl = item.url;//扩展个字段用于修改URL提交
          return item
        }
        if (add) {
          this.jsonData.unshift(mat(data));
          this.tip('添加成功');
          this.clean();
        } else {
          data.map(mat)
          this.showData = this.jsonData = this.jsonData.concat(data);
        }

      },
    },
  };
</script>



